___CSS_LOADER_EXPORT___.push([module.id, `.chat-interface {
    display: flex;
    flex-direction: column;
    height: 100vh;
    background: var(--vscode-editor-background);
    position: relative;
    font-family: var(--vscode-font-family);
}

.chat-interface.compact {
    font-size: 13px;
}

/* --- Header --- */
.chat-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 12px 16px;
    background: var(--vscode-editor-background);
    /* Keep it solid or subtle */
    border-bottom: 1px solid var(--vscode-panel-border);
    height: 50px;
    box-sizing: border-box;
    z-index: 10;
}

.header-left {
    display: flex;
    align-items: center;
    gap: 12px;
}

.header-actions {
    display: flex;
    align-items: center;
    gap: 8px;
}

.separator-vertical {
    width: 1px;
    height: 20px;
    background: var(--vscode-panel-border);
    margin: 0 4px;
}

.icon-btn {
    background: transparent;
    border: none;
    border-radius: 6px;
    width: 32px;
    height: 32px;
    display: flex;
    align-items: center;
    justify-content: center;
    color: var(--vscode-icon-foreground);
    cursor: pointer;
    transition: all 0.2s;
    opacity: 0.7;
}

.icon-btn:hover {
    background: var(--vscode-toolbar-hoverBackground);
    opacity: 1;
}

.icon-btn.danger:hover {
    background: rgba(255, 59, 48, 0.1);
    color: #ff3b30;
}

/* --- Dashboard Overlay --- */
.dashboard-overlay {
    position: absolute;
    top: 50px;
    left: 0;
    right: 0;
    bottom: 0;
    background: var(--vscode-editor-background);
    z-index: 20;
    overflow-y: auto;
    animation: fadeIn 0.15s ease-out;
}

@keyframes fadeIn {
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}


/* --- Main Content --- */
/* MessageList is handled by its own component but should fill space */
.message-list {
    flex: 1;
    overflow-y: auto;
}


/* --- Footer --- */
.chat-footer {
    padding: 16px;
    background: var(--vscode-editor-background);
    border-top: 1px solid var(--vscode-panel-border);
    display: flex;
    flex-direction: column;
    gap: 12px;
}

.model-bar {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    padding-left: 2px;
}

/* --- Responsive --- */
@media (max-width: 600px) {
    .chat-footer {
        padding: 12px;
    }
}

/* --- Tool Status --- */
.tool-status-container {
    background: rgba(30, 30, 30, 0.95);
    border-top: 1px solid var(--primary-color);
    padding: 0;
    font-size: 0.9em;
    color: var(--vscode-foreground);
    animation: slideUp 0.3s ease;
    max-height: 200px;
    overflow-y: auto;
}

.tool-status-header {
    display: flex;
    align-items: center;
    gap: 10px;
    padding: 8px 16px;
    font-weight: 600;
    background: rgba(255, 255, 255, 0.05);
    border-bottom: 1px solid rgba(255, 255, 255, 0.05);
}

.tool-list {
    padding: 8px 16px;
    display: flex;
    flex-direction: column;
    gap: 6px;
}

.tool-item {
    display: flex;
    align-items: center;
    gap: 8px;
    opacity: 0.7;
    transition: opacity 0.2s;
}

.tool-item.running {
    opacity: 1;
    color: var(--primary-color);
}

.tool-item.completed {
    opacity: 0.8;
    color: #4CAF50;
    text-decoration: line-through;
    /* Optional for completed look */
}

.tool-icon {
    width: 16px;
    text-align: center;
}

.tool-path {
    font-family: monospace;
    opacity: 0.8;
}

.tool-spinner {
    width: 14px;
    height: 14px;
    border: 2px solid var(--primary-color);
    border-top-color: transparent;
    border-radius: 50%;
    animation: spin 1s linear infinite;
}

@keyframes spin {
    to {
        transform: rotate(360deg);
    }
}

@keyframes slideUp {
    from {
        opacity: 0;
        transform: translateY(10px);
    }

    to {
        opacity: 1;
        transform: translateY(0);
    }
}`, "",{"version":3,"sources":["webpack://./src/webview/components/ChatInterface.css"],"names":[],"mappings":"AAAA;IACI,aAAa;IACb,sBAAsB;IACtB,aAAa;IACb,2CAA2C;IAC3C,kBAAkB;IAClB,sCAAsC;AAC1C;;AAEA;IACI,eAAe;AACnB;;AAEA,mBAAmB;AACnB;IACI,aAAa;IACb,8BAA8B;IAC9B,mBAAmB;IACnB,kBAAkB;IAClB,2CAA2C;IAC3C,4BAA4B;IAC5B,mDAAmD;IACnD,YAAY;IACZ,sBAAsB;IACtB,WAAW;AACf;;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,SAAS;AACb;;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,QAAQ;AACZ;;AAEA;IACI,UAAU;IACV,YAAY;IACZ,sCAAsC;IACtC,aAAa;AACjB;;AAEA;IACI,uBAAuB;IACvB,YAAY;IACZ,kBAAkB;IAClB,WAAW;IACX,YAAY;IACZ,aAAa;IACb,mBAAmB;IACnB,uBAAuB;IACvB,oCAAoC;IACpC,eAAe;IACf,oBAAoB;IACpB,YAAY;AAChB;;AAEA;IACI,iDAAiD;IACjD,UAAU;AACd;;AAEA;IACI,kCAAkC;IAClC,cAAc;AAClB;;AAEA,8BAA8B;AAC9B;IACI,kBAAkB;IAClB,SAAS;IACT,OAAO;IACP,QAAQ;IACR,SAAS;IACT,2CAA2C;IAC3C,WAAW;IACX,gBAAgB;IAChB,gCAAgC;AACpC;;AAEA;IACI;QACI,UAAU;IACd;;IAEA;QACI,UAAU;IACd;AACJ;;;AAGA,yBAAyB;AACzB,sEAAsE;AACtE;IACI,OAAO;IACP,gBAAgB;AACpB;;;AAGA,mBAAmB;AACnB;IACI,aAAa;IACb,2CAA2C;IAC3C,gDAAgD;IAChD,aAAa;IACb,sBAAsB;IACtB,SAAS;AACb;;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,2BAA2B;IAC3B,iBAAiB;AACrB;;AAEA,uBAAuB;AACvB;IACI;QACI,aAAa;IACjB;AACJ;;AAEA,wBAAwB;AACxB;IACI,kCAAkC;IAClC,0CAA0C;IAC1C,UAAU;IACV,gBAAgB;IAChB,+BAA+B;IAC/B,4BAA4B;IAC5B,iBAAiB;IACjB,gBAAgB;AACpB;;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,SAAS;IACT,iBAAiB;IACjB,gBAAgB;IAChB,qCAAqC;IACrC,kDAAkD;AACtD;;AAEA;IACI,iBAAiB;IACjB,aAAa;IACb,sBAAsB;IACtB,QAAQ;AACZ;;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,QAAQ;IACR,YAAY;IACZ,wBAAwB;AAC5B;;AAEA;IACI,UAAU;IACV,2BAA2B;AAC/B;;AAEA;IACI,YAAY;IACZ,cAAc;IACd,6BAA6B;IAC7B,gCAAgC;AACpC;;AAEA;IACI,WAAW;IACX,kBAAkB;AACtB;;AAEA;IACI,sBAAsB;IACtB,YAAY;AAChB;;AAEA;IACI,WAAW;IACX,YAAY;IACZ,sCAAsC;IACtC,6BAA6B;IAC7B,kBAAkB;IAClB,kCAAkC;AACtC;;AAEA;IACI;QACI,yBAAyB;IAC7B;AACJ;;AAEA;IACI;QACI,UAAU;QACV,2BAA2B;IAC/B;;IAEA;QACI,UAAU;QACV,wBAAwB;IAC5B;AACJ","sourcesContent":[".chat-interface {\n    display: flex;\n    flex-direction: column;\n    height: 100vh;\n    background: var(--vscode-edit